
.title{
  background: url('../../assets/images/pro_tb.png')no-repeat 0.24rem ;
}
.choose-content{
  width: 100%;
  height: auto;
  border-top: 1px solid #e5e5e5;
  padding: 0 0.24rem;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.choose-items{
  padding: 0.36rem 0;
  width: 100%;
  height: auto;
  /*background: black;*/
}
/*头像*/
.author{
  width: 100%;
}
.author-avatar{
  width: 0.8rem;
  height: 0.8rem;
  border-radius: 50%;
  position: relative;
}
.author-avatar-svip{
  position: absolute; bottom: 0px; right: -5px;
  width: .3rem;
  height: .3rem;
}
.author-avatar-content{
  height: 100%;
  margin-left: 0.16rem;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
}
.author-name{
  height: 60%;
}
.author-name-badge{
  width: .4rem;
  height: .4rem;
  margin: 0 0.05rem;
  display: inline-block;
}
.author-name-vip{
  color: red !important;
}
.author-date{
  height: 40%;
}
/*文字 两行*/
.author-text{
  width: 100%;
  line-height: 1.5;
}
/*图片*/
.author-pic-content{
  width: 100%;
}
.author-pic-items{
  width: 33%;
  height: auto;
  margin-right: 2%;
  /*background: black;*/
}
.author-pic-items img{
  width: 100%;
  /*display: none;*/
}
/*查看更多*/
.choose-getMore{
  padding: 0.32rem;
  text-align: center;
  border-top: 1px solid #e5e5e5;
}



