
.title{
  background: url('../../assets/images/pro_tb.png')no-repeat 0.24rem ;
}
.video-choose-content{
  width: 100%;
  height: auto;
  /*border-top: 1px solid #e5e5e5;*/
  padding: 0 0.24rem;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.video-choose-items{
  padding-top: 0.36rem;
  width: 100%;
  height: auto;
}
/*视频图*/
.video-pic{
  width: 2.76rem;
  height: 1.8rem;

  position: relative;
  -webkit-flex: none;
}
.video-pic>img:first-child{
  width: 100%;
  height: 100%;
  border-radius: 0.08rem;
}
.video-playlogo{
  width: 0.8rem;
  height: 0.8rem;
  position: absolute;
  left: 50%;
  top: 50%;
  opacity: 0.8;
  -webkit-transform: translate(-50%,-50%);
  transform: translate(-50%,-50%);
}
.video-content{
  margin-left: .16rem;
}
.video-item{
}
/*底栏*/
.video-footer{
  width: 100%;
}
.video-footer-icon{
  width: 0.36rem;
  height: 0.36rem;
  margin-right: 0.08rem;
  opacity: 0.6;
}
/*查看更多*/
.choose-getMore{
  padding: 0.32rem;
  text-align: center;
  border-top: 1px solid #e5e5e5;
}
/*渐变标签*/
.author-tag{
  display: inline-block;
  padding: .02rem .12rem;
  vertical-align: top;
  font-size: .2rem;
  letter-spacing: .04rem;
  color: #fff;
  border-radius: .08rem;
  -webkit-flex: none;
  margin-top: 0.08rem;
  -webkit-transform : scale(0.84,0.84) ;
}
.author-tag-yellow {
  background-image: linear-gradient(90deg,#ffab3e,#ffcc8b);
}
.author-tag-purple {
  background-image: linear-gradient(90deg,#d06bdc,#e4a9eb);
}
.author-tag-blue {
  background-image: linear-gradient(90deg,#216ff0,#699df5);
}



