
.title{
  background: url('../../assets/images/pro_tb.png')no-repeat 0.24rem ;
}
.choose-content{
  width: 100%;
  height: auto;
  border-top: 1px solid #e5e5e5;
  padding: 0 0.24rem;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.choose-items{
  padding: 0.36rem 0;
  width: 100%;
  height: auto;
  /*background: black;*/
}
/*头像*/
.author{
  width: 100%;
}
.author-avatar{
  width: 0.8rem;
  height: 0.8rem;
  border-radius: 50%;
  position: relative;
}
.author-avatar-svip{
  position: absolute; bottom: 0px; right: -5px;
  width: .3rem;
  height: .3rem;
}
.author-avatar-content{
  height: 100%;
  margin-left: 0.16rem;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
}
.author-name{
  height: 60%;
}
.author-name-badge{
  width: .4rem;
  height: .4rem;
  margin: 0 0.05rem;
  display: inline-block;
}
.author-name-vip{
  color: red !important;
}
.author-date{
  height: 40%;
}
/*主题 一行*/
.author-topic{
  width: 100%;
  line-height: 1.5;
}
.author-topic img{
  display: inline-block;
  width: .44rem;
  height: .44rem;
  vertical-align: middle;
  margin-right: 0.08rem;
}
/*底栏*/
.author-footer{
  width: 100%;
  margin-top: .32rem
}
.author-footer-looker{
  /*height: auto;*/
  /*margin-right: 2%;*/
  /*background: black;*/
}
.author-footer-others>div:nth-child(n+2){
  margin-left: 0.16rem;
}
.author-footer-icons{
  width: 0.36rem;
  height: 0.36rem;
  margin-right: 0.08rem;
}
/*查看更多*/
.choose-getMore{
  padding: 0.32rem;
  text-align: center;
  border-top: 1px solid #e5e5e5;
}
/*渐变标签*/
.author-tag{
  display: inline-block;
  padding: .02rem .12rem;
  vertical-align: top;
  font-size: .2rem;
  letter-spacing: .04rem;
  color: #fff;
  border-radius: .08rem;
  -webkit-flex: none;
}
.author-tag-yellow {
  background-image: linear-gradient(90deg,#ffab3e,#ffcc8b);
}
.author-tag-purple {
  background-image: linear-gradient(90deg,#d06bdc,#e4a9eb);
}
.author-tag-blue {
  background-image: linear-gradient(90deg,#216ff0,#699df5);
}



